
//$Blue: #6789d3;
$Blue: #3867ce; // recebe cor do design system 
$BlueHover: darken($Blue,30%);
$DarkBlue: #00314c;
$Gray: #403d39;
$LightGray: #f7f6f4;
$White: #fff;
$radius: 4px;

/* Breakpoints */
$onlyMobile: "all and (max-width: 767px)"; 
$minTablet: "all and (min-width: 768px)";
$onlyTablet: "all and (min-width: 768px) and (max-width: 989px)";


$maxTablet: "all and (max-width: 989px)"; 
$maxTablet2: "all and (max-width: 1205px)"; 

$minDesktop: "all and (min-width: 990px)";
$onlyDesktop: "all and (min-width: 990px) and (max-width: 1205px)";
$onlyHighDef: "all and (min-width: 1205px)";

