@mixin radius($rad: $radius){
	border-radius: $rad;
}
@mixin shadow($def: 0px 0px 4px rgba(0, 0, 0, 0.4)){
	-webkit-box-shadow: $def;
	-moz-box-shadow:    $def;
	box-shadow:         $def;
}
.border-box {
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
@mixin border-box {
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
