}
*/
.cssAnimations {
	@mixin animated($time: .5s, $prop: background, $prop2: text-indent){
		-webkit-animation-fill-mode: both;
		-moz-animation-fill-mode: both;
		-ms-animation-fill-mode: both;
		-o-animation-fill-mode: both;
		animation-fill-mode: both;
		-webkit-animation-duration: $time;
		-moz-animation-duration: $time;
		-ms-animation-duration: $time;
		-o-animation-duration: $time;
		animation-duration: $time;

		-o-transition: $prop $time ease-out, $prop2 $time ease-out;
		-ms-transition: $prop $time ease-out, $prop2 $time ease-out;
		-moz-transition: $prop $time ease-out, $prop2 $time ease-out;
		-webkit-transition: $prop $time ease-out, $prop2 $time ease-out;
		transition: $prop $time ease-out, $prop2 $time ease-out;
	}

	@-webkit-keyframes bounce {
		0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);}
		40% {-webkit-transform: translateY(30px);}
		60% {-webkit-transform: translateY(15px);}
	}

	@-moz-keyframes bounce {
		0%, 20%, 50%, 80%, 100% {-moz-transform: translateY(0);}
		40% {-moz-transform: translateY(30px);}
		60% {-moz-transform: translateY(15px);}
	}

	@-ms-keyframes bounce {
		0%, 20%, 50%, 80%, 100% {-ms-transform: translateY(0);}
		40% {-ms-transform: translateY(30px);}
		60% {-ms-transform: translateY(15px);}
	}

	@-o-keyframes bounce {
		0%, 20%, 50%, 80%, 100% {-o-transform: translateY(0);}
		40% {-o-transform: translateY(30px);}
		60% {-o-transform: translateY(15px);}
	}
	@keyframes bounce {
		0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
		40% {transform: translateY(30px);}
		60% {transform: translateY(15px);}
	}

	@mixin bounce($time: .5s){
		-webkit-animation-name: bounce;
		-moz-animation-name: bounce;
		-ms-animation-name: bounce;
		-o-animation-name: bounce;
		animation-name: bounce;

		-webkit-animation-duration: $time;
		-moz-animation-duration: $time;
		-ms-animation-duration: $time;
		-o-animation-duration: $time;
		animation-duration: $time; 
	}

	@mixin slideInUp($time: .5s){
		-webkit-animation-name: slideInUp;
		animation-name: slideInUp;

		-webkit-animation-duration: $time;
		-moz-animation-duration: $time;
		-ms-animation-duration: $time;
		-o-animation-duration: $time;
		animation-duration: $time;
	}

	@-webkit-keyframes slideInUp {
		from {
			-webkit-transform: translate3d(0, 30%, 0);
			transform: translate3d(0, 30%, 0);
			visibility: visible;
			opacity: 0;
		}

		to {
			-webkit-transform: translate3d(0, 0, 0);
			transform: translate3d(0, 0, 0);
			opacity: 1;
		}
	}
}

